.TableItem {
  @include column-flexbox(center, start);

  width: 100%;
  max-width: 960px;
  padding: 20px 0;

  .back {
    padding: 20px;
    font-weight: 600;
    margin-bottom: 20px;

    &:hover,
    &:active {
      background-color: $background-banner;
    }
  }

  &-desc {
    white-space: pre-line;
  }

  &-contents {
    @include column-flexbox(start, start);
    column-gap: 50px;

    width: 100%;
    margin-bottom: 100px;

    .content {
      &-header {
        @include flexbox(between);
        width: 100%;
        border-top: 1px solid gray;
        border-bottom: 1px solid gray;
        padding: 20px;
        margin-bottom: 100px;
      }

      &-title-group {
        @include flexbox();
        .category {
          font-size: 13px;
          font-weight: 800;
          color: $blue-main;
          margin-right: 20px;
        }

        .title {
          font-size: 20px;
          font-weight: 600;
          color: $primary;
        }
      }

      &-date {
        font-size: 14px;
        color: $border;
      }

      &-info {
        @include column-flexbox();
        gap: 30px;
        margin: 0 auto;
        width: 100%;
        padding: 0 20px;

        &.image {
          width: 50vw;
          padding: 0;
        }

        .image-box {
          display: block;
          width: 100%;
          height: 100%;
          overflow: hidden;
          border-radius: 10px;

          img {
            display: block;
            width: 100%;
            max-height: 500px;
            object-fit: cover;
            object-position: center top;
          }
        }
      }
    }
  }

  &-desc {
    @include column-flexbox(between, start);
    width: 100%;
    flex-grow: 0;
    margin-bottom: 20px;
    font-size: 16px;
    line-height: 1.625;
    white-space: pre-line;
    word-break: keep-all;
    // padding: 0 20px;
  }

  &-pagination {
    @include column-flexbox(start, start);
    width: 100%;

    .page-item {
      @include flexbox(start);
      width: 100%;
      padding: 0 20px;
      font-size: 15px;
      background-color: $background-banner;
      border-radius: 12px;
      margin-bottom: 10px;

      span {
        display: block;
        min-width: 30px;
        margin-right: 20px;
        font-weight: 700;
      }

      a {
        display: block;
        width: 100%;
        padding: 30px 0;
        transition: all 350ms ease-in-out;
      }
    }

    .disabled {
      color: $border;
      pointer-events: none;
    }
  }

  @include responsive(T) {
    &-contents {
      @include column-flexbox(start, start);
      column-gap: 50px;

      width: 100%;
      margin-bottom: 100px;

      .content {
        &-title-group {
          .category {
            font-size: 12px;
          }
        }

        &-info {
          &.image {
            width: 100%;
            padding: 0 20px;
          }
        }
      }
    }

    gap: 0;
  }

  @include responsive(M) {
    &-contents {
      @include column-flexbox(start, start);
      column-gap: 50px;

      width: 100%;
      margin-bottom: 100px;

      .content {
        &-header {
          padding: 15px 20px;
        }

        &-title-group {
          .category {
            font-size: 10px;
          }

          .title {
            font-size: 16px;
          }
        }

        &-date {
          display: none;
        }

        &-info {
          padding: 0;
          &.image {
            width: 100%;
            padding: 0;
          }
        }

        &-desc {
          font-size: 14px;
          line-height: 1.5;
        }
      }
    }

    gap: 0;
  }
}
